import _ from "lodash";
import { getSessionStorageItem, setSessionStorageItem, crypto } from "@/utils";
import { SID } from "@/dict";

/**
 *  sessionStorage key
 * @param {Object} params
 * @returns {string}
 */
const createSSKey = params => {
  delete params.sync;
  return `q-${params.table.name}-${crypto
    .MD5(JSON.stringify({ ...params, SID }))
    .toString()}`;
};

/**
 * , { [key]: request } 
 * 
 * : delete requestCache[key];
 */
const requestCache = {};

/**
 * 
 * @param {Object} request - Axios
 * @param {string} type - "all" | "list"
 * @param {Object} data - 
 * @param {number} data.current - , type  list 1
 * @param {number} data.pageSize - , type  list 
 * @param {Object} data.table - idnamecolumn_id 
 * @param {number} data.table.id - id0
 * @param {string} data.table.name - 
 * @param {string} data.table.as_name - 
 * @param {number} data.table.column_id - id
 * @param {boolean} data.table.no_default_field - idstatecreated_atupdated_at
 * @param {(string|number)[]} data.table.fields - [field1, field2, fieldId1, ...]
 * @param {[(string|number),string,any][][]} data.table.filters -  [[[field1, "=", "0"], [field2, "!=", "0"], ...], [[field3, "=", "1"], ...]] => field1=0 and field2!=0 or field3=1
 * @param {[(string|number),string][]} data.table.order_by -  [[field1, "ASC"], [field2, "DESC"]]
 * @param {Object[]} data.join_tables - , [join_table], join_table  table 
 * @param {Object[]} data.left_join_tables - , [left_join_table], left_join_table  table 
 * @returns {Promise}
 */
export const query = (request, type, data) =>
  new Promise((resolve, reject) => {
    const sync = data.sync;
    const key = createSSKey(data);

    if (!sync) {
      const value = getSessionStorageItem(key);
      if (!_.isEmpty(value)) {
        return resolve(value);
      }
    }

    let req = requestCache[key];
    if (!req) {
      // 
      requestCache[key] = request.post(`/query/${type}`, data).then(res => {
        setSessionStorageItem(key, res);
        return res;
      });
      req = requestCache[key];
    }

    req
      .then(resolve)
      .catch(reject)
      .finally(() => {
        // 
        delete requestCache[key];
      });
  });

export default query;
