/**
 * Image
 * @function
 * @param {Image} image    Image
 * @param {Number} width   
 * @param {Number} height  
 * @param {Number} quality 0~11
 * @returns Promise; then(blob=>{})
 */
export const imageToFile = ({ image, width, height, quality }) =>
  new Promise(resolve => {
    const cvs = document.createElement("canvas");
    const ctx = cvs.getContext("2d");
    cvs.width = width;
    cvs.height = height;
    ctx.drawImage(image, 0, 0, width, height);
    cvs.toBlob(resolve, "image/webp", quality || 0.8);
  });

/**
 * urlImage
 * @function
 * @param {String} url url
 * @returns Promisethen(image=>{}).catch(err=>{})
 */
export const urlToImage = url =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.addEventListener("load", () => {
      resolve(img);
    });
    img.addEventListener("error", reject);
    img.src = url;
  });

/**
 * url
 * @function
 * @param {File} file 
 * @returns Promisethen(url=>{}).catch(err=>{})
 */
export const fileToUrl = file =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.addEventListener("load", () => {
      resolve(reader.result);
    });
    reader.addEventListener("error", reject);
    reader.readAsDataURL(file);
  });

/**
 * 
 * @function
 * @param   {File}    file    
 * @param   {Number}  width   
 * @param   {Number}  height  
 * @param   {Number}  quality 0~11
 * @returns Promisethen(blob=>{})
 */
export const compressImage = ({ file, width, height, quality }) =>
  new Promise(resolve => {
    // file -> Image -> blob
    fileToUrl(file).then(url => {
      urlToImage(url).then(image => {
        if (!width) {
          width = image.width > 1200 ? 1200 : image.width;
        }
        if (!height) {
          height = image.height / (image.width / width);
        }
        imageToFile({ image, width, height, quality }).then(resolve);
      });
    });
  });
