import _ from "lodash";

/**
 * 
 * @param {string} newItemValue
 */
const handleClear = newItemValue => {
  let size = newItemValue.length;
  _.forOwn(sessionStorage, value => {
    size += value.length;
  });
  size > 4.5 << 20 && sessionStorage.clear();
};

/**
 *  sessionStorage
 * @param {string} key
 * @param {any} value -  JSON.stringify 
 */
export const setSessionStorageItem = (key, value) => {
  if (_.isObject(value)) {
    value = JSON.stringify(value);
  }

  handleClear(value);

  sessionStorage.setItem(key, value);
};

/**
 *  sessionStorage
 * @param {string} key
 * @returns {any}  JSON.parse 
 */
export const getSessionStorageItem = key => {
  let value = sessionStorage.getItem(key);
  try {
    value = JSON.parse(value);
  } catch (err) {
    // no todo
  }

  return value;
};
